<template>  
    <div v-if="html" v-html="html" class="markdown-body"></div>  
    <div v-else>Loading......</div>  
</template>  
  
<script>  
import {marked} from 'marked';   
import devlog_2_0 from '@/components/markdown/devlog_2_0.md';  
  
export default {  
    data() {  
        return {  
            html: ''  
        };  
    },  
    created() {  
        // 在组件创建时转换Markdown为HTML  
        this.html = marked(devlog_2_0);  
    }  
}  
</script>

<style scoped>
@import url("~github-markdown-css/github-markdown.css");

* {
    width: 1200px;
    margin: auto;
    text-align: start;
}
</style>